@use "base/variables" as v;
@use "base/mixins" as m;

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  margin: 1rem auto;
  @include m.tablet {
    width: 75%;
    @include m.grid(2, 2rem);
  }
  &__text,
  &__email,
  &__subject,
  &__message {
    margin: 1rem 0;
    padding: 0.5rem;
  }

  &__subject,
  &__message,
  &__btns {
    @include m.tablet {
      grid-column: 1 / 3;
    }
  }

  &__btns {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    button {
      margin: 1rem 0;
      padding: 1rem;
      width: 15rem;
      border: none;
      color: v.$blanco;
      border-radius: 1rem;
      cursor: pointer;
      transition: transform 0.3s ease;
      &:hover {
        transform: scale(1.1);
      }
      i {
        margin-right: 1rem;
      }
    }
    #submit {
      background-color: #189b3a;
    }
    #reset {
      background-color: v.$secundario;
    }
  }
}
