//Fuentes
$fuentePrincipal: "Red Hat Display", sans-serif;
$fuenteTitulo: "IBM Plex Sans", sans-serif;
$fuenteCursiva: "Permanent Marker", cursive;

//Media Queries
$mobile: 480px;
$tablet: 768px;
$desktop: 1024px;

//colores
$negro: #121212;
$primario: #000022;
$secundario: #001242;
$links: #0094c6;
$linkHover: #005e7c;
$botones: #040f16;
$blanco: #fff;
$gris: #c1c1c1c1;
$error: #ca1414;
$bgError: rgba(255, 0, 0, 0.2);
$success: #2dd275;
$bgSuccess: rgba(0, 255, 0, 0.2);

//Unidades
$separacion: 5rem;

// Gradient
$gradientToLeft: linear-gradient(
  to left bottom,
  #000022,
  #03052a,
  #030a32,
  #010f3a,
  #001242
);

$gradientToRight: linear-gradient(
  to right bottom,
  #000022,
  #03052a,
  #030a32,
  #010f3a,
  #001242
);
