@use "base/variables" as v;
@use "base/mixins" as m;

.container {
  position: relative;
  background-color: v.$secundario;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
  height: 100vh;

  &__back-arrow {
    position: absolute;
    left: 0;
    top: 0;
    font-size: 4rem;
    padding: 1rem;
    a {
      color: v.$blanco;
    }
  }
}
