@use "base/variables" as v;
@use "base/mixins" as m;

.sidebar {
  position: fixed;
  width: 25rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: v.$gradientToLeft;
  top: 0;
  left: 0;
  z-index: 100;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  overflow: scroll;
  @include m.desktop {
    transform: translateX(0);
  }
  &__header {
    padding-top: 1rem;
  }
  &__logo {
    width: 15rem;
    height: 15rem;
    margin: 0 auto;
    border-radius: 50%;
    margin-bottom: 1rem;
  }
  &__slogan {
    text-align: center;
    font-family: v.$fuenteCursiva;
    font-size: 2rem;
    color: v.$blanco;
    margin: 0;
  }
  &__footer {
    width: 100%;
    margin: 1rem 0 0 0;
    p {
      font-size: 1.2rem;
      text-align: center;
      color: v.$blanco;
      margin: 0;
    }
  }
}
.open {
  transform: translateX(0);
}
