@use "base/variables" as v;
@use "base/mixins" as m;

.works {
  width: 90%;
  min-height: 25rem;
  overflow: hidden;
  margin: 2rem auto;
  @include m.telefono {
    min-height: 40rem;
  }
  @include m.tablet {
    min-height: 55rem;
  }
  &__slider {
    position: relative;
    height: max-content;
  }
  &__image {
    position: absolute;
    clip-path: circle(0% at 0 50%);
    &--current {
      clip-path: circle(150% at 0 50%);
      transition: 2s;
    }
  }
}
