@use "base/variables" as v;
@use "base/mixins" as m;

.main {
  &__thanks {
    color: v.$blanco;
  }
  &__follow-me {
    h3 {
      color: v.$blanco;
    }
  }
  &__social-icons {
    width: 75%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      color: v.$blanco;
      font-size: 2.5rem;
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      border: 0.1rem solid v.$blanco;
      transition: all 0.3s ease;
      &:nth-child(1) {
        &:hover {
          background-color: #c32aa3;
          transform: scale(1.2);
          border-color: #c32aa3;
        }
      }
      &:nth-child(2) {
        &:hover {
          background-color: #1da1f2;
          transform: scale(1.2);
          border-color: #1da1f2;
        }
      }
      &:nth-child(3) {
        &:hover {
          background-color: #1877f2;
          transform: scale(1.2);
          border-color: #1877f2;
        }
      }
      &:nth-child(4) {
        &:hover {
          background-color: #000000;
          transform: scale(1.2);
          border-color: #000000;
        }
      }
    }
  }
}
