@use "base/variables" as v;
@use "base/mixins" as m;

.portfolio {
  @include m.desktop {
    margin-left: 25rem;
  }
  &__header {
    background-image: url(../img/header-page.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 90%;
    margin: 2rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10rem;
    border-radius: 2rem 6rem;
  }
  &__heading {
    width: 30rem;
    margin: 0;
    border-bottom: solid 0.3rem v.$blanco;
    color: v.$blanco;
  }
  &__content {
    margin: 2rem 1rem;
  }
}
