@use "base/variables" as v;
@use "base/mixins" as m;

.contact {
  height: 70rem;
  @include m.desktop {
    margin-left: 25rem;
  }
  &__header {
    background-image: url(../img/header-page.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 90%;
    margin: 2rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 7.5rem;
    border-radius: 2rem 6rem;
    @include m.tablet {
      height: 10rem;
    }
  }
  &__heading {
    width: 20rem;
    font-size: 2.8rem;
    margin: 0;
    border-bottom: solid 0.3rem v.$blanco;
    color: v.$blanco;
    @include m.tablet {
      width: 30rem;
      font-size: 3.5rem;
    }
  }
  &__text {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    color: v.$blanco;
  }
  &__content-form {
    width: 90%;
    height: 60rem;
    margin: auto;
    padding: 2rem 0;
    background-color: v.$linkHover;
    @include m.tablet {
      height: 55rem;
    }
  }
}
